import hljs from "highlight.js"
import "highlight.js/styles/github-gist.css"
import $ from "jquery"
import "jquery-ui"
import React, { Component } from "react"
import Remarkable from "remarkable"
import {
    baseURL,
    /* GANASAKTI_CATEGORIES */
    GANASAKTI_MARKDOWN_TUTORIAL,
    uploadImagesPHP,
    uploadPostPHP,
    uploadThumbnailPHP,
    url
} from "../../AppClass"
import "../../stylesheets/markdown.css"

let imageUrlInsideArticle = ""
let imageFileNamesForDB = ""
class MarkDown extends Component {
    constructor(props) {
        super(props)

        this.state = {
            title: "[ TITLE OF THE ARTICLE ]",
            author: "[ AUTHOR NAME ]",
            date: "1970-01-01",
            article: "[ ARTICLE CONTENT ]",
            category: "news",
            youtubeLink: "",
            // thumbnail: "",
            showMarkdownTutorial: false,
            tutorialState: GANASAKTI_MARKDOWN_TUTORIAL,
            serverResponse: "",
            openSnackbar: false,
            labelWidth: 100
        }

        this.InputLabelRef = React.createRef()
    }

    componentDidMount() {
        this.setState({
            labelWidth: ReactDOM.findDOMNode(this.InputLabelRef).offsetWidth
        })
    }

    handleClose = () => {
        this.setState({ showMarkdownTutorial: false })
    }

    handleChange = name => event => {
        this.setState({ [name]: event.target.value }, function() {
            // console.log(this.state)
        })
    }

    getRawMarkup(passedState) {
        var md = new Remarkable("full", {
            html: true, // Enable HTML tags in source
            xhtmlOut: false, // Use '/' to close single tags (<br />)
            breaks: false, // Convert '\n' in paragraphs into <br>
            langPrefix: "language-", // CSS language prefix for fenced blocks
            linkify: true, // autoconvert URL-like texts to links
            linkTarget: "", // set target to open link in

            // Enable some language-neutral replacements + quotes beautification
            typographer: true,

            // Double + single quotes replacement pairs, when typographer enabled,
            // and smartquotes on. Set doubles to '«»' for Russian, '„“' for German.
            quotes: "“”‘’",

            // Highlighter function. Should return escaped HTML,
            // or '' if input not changed
            highlight: function(str, lang) {
                if (lang && hljs.getLanguage(lang)) {
                    try {
                        return hljs.highlight(lang, str).value
                    } catch (__) {}
                }

                try {
                    return hljs.highlightAuto(str).value
                } catch (__) {}

                return "" // use external default escaping
            }
        })

        return { __html: md.render(passedState) }
    }

    showMarkdownTutorial = () => {
        this.setState({
            showMarkdownTutorial: true
        })
    }

    imageUrlAdded = urlArray => {
        // console.log("urls in MarkDown: ", urlArray)

        let baseUrl = url + "/images/"

        urlArray.map(url => {
            imageUrlInsideArticle =
                imageUrlInsideArticle + "![](" + baseUrl + url + ")" + "\n"
        })

        // console.log(imageUrlInsideArticle)

        this.setState({
            article: this.state.article + "\n\n" + imageUrlInsideArticle + "\n\n"
        })

        // add uploaded images into a string for DB storing
        urlArray.map(fileName => {
            if (imageFileNamesForDB === "") {
                imageFileNamesForDB = fileName
            } else {
                imageFileNamesForDB = imageFileNamesForDB + "," + fileName
            }
        })

        // this.setState({
        //     article: [...this.state.article, urlArray]
        // })
    }

    // thumbnailUrlAdded = thumbnailUrl => {
    //     // console.log(thumbnailUrl)
    //     // let baseUrl = url + "/images/"
    //     // let thumbnailUrl = baseUrl + thumbnail_url

    //     // remove the thumbnail/ part from thumbnail_url
    //     let processedThumbnailUrl = thumbnailUrl[0].split("/")[1]
    //     // console.log(processedThumbnailUrl)
    //     this.setState({
    //         thumbnail: processedThumbnailUrl.toString()
    //     })
    // }

    onUploadPost = () => {
        let id = JSON.parse(sessionStorage.getItem("userData")).id

        $.ajax({
            url: uploadPostPHP,
            type: "POST",
            data: {
                title: this.state.title,
                author: this.state.author,
                date: this.state.date,
                article: this.state.article,
                category: this.state.category,
                youtubeLink: this.state.youtubeLink,
                imageLinks: imageFileNamesForDB,
                id: id
                // thumbnail: this.state.thumbnail
            },
            success: function(res) {
                if (res === "failure") {
                    alert("Please check all the fields and try again.")
                } else {
                    let parsedRes = JSON.parse(res)
                    if (parsedRes.status === "success") {
                        alert(`successfully uploaded.`)

                        // clear variables
                        imageFileNamesForDB = ""
                        imageUrlInsideArticle = ""
                    } else {
                        alert("Problem occured while uploading. Please try again. ")
                    }

                    // let parsedRes = JSON.parse(res)

                    // this.setState({
                    //     serverResponse: parsedRes,
                    //     openSnackbar: true
                    // })
                }
            }.bind(this)
        })
    }

    render() {
        const { classes } = this.props
        return (
            <div className={classes.spacer}>
                <Grid container spacing={16}>
                    <Grid item xs={12} sm={6}>
                        <h1>Markdown Editor</h1>
                        <Grid container spacing={16}>
                            <Grid item xs={12} sm={12}>
                                <TextField
                                    label="Title of the article"
                                    placeholder="Enter title"
                                    className={classes.article}
                                    value={this.state.title}
                                    onChange={this.handleChange("title")}
                                    required
                                    variant="outlined"
                                />
                            </Grid>

                            <Grid item xs={12} sm={12}>
                                <TextField
                                    label="Author name"
                                    placeholder="Enter the name of author"
                                    className={classes.article}
                                    value={this.state.author}
                                    onChange={this.handleChange("author")}
                                    required
                                    variant="outlined"
                                />
                            </Grid>

                            <Grid item xs={12} sm={12}>
                                <TextField
                                    id="date"
                                    label="Date"
                                    type="date"
                                    // className={classes.textField}
                                    InputLabelProps={{
                                        shrink: true
                                    }}
                                    value={this.state.date}
                                    onChange={this.handleChange("date")}
                                    variant="outlined"
                                />
                            </Grid>

                            {/* UPLOAD IMAGES */}
                            <ImageUpload
                                cssProps={classes}
                                imageUrlAdded={this.imageUrlAdded}
                            />

                            <Grid item xs={12} sm={12}>
                                <TextField
                                    label="Article"
                                    multiline
                                    rows="15"
                                    placeholder="Write here..."
                                    className={classes.article}
                                    required
                                    value={this.state.article}
                                    onChange={this.handleChange("article")}
                                    variant="outlined"
                                />

                                {/* add Markdown tutorial */}
                                <Tooltip
                                    title="How to write Rich Text?"
                                    placement="right"
                                >
                                    <IconButton
                                        className={classes.button}
                                        aria-label="Info"
                                        onClick={this.showMarkdownTutorial}
                                    >
                                        <Info />
                                    </IconButton>
                                </Tooltip>
                            </Grid>

                            {/* TUTORIAL DIALOG */}
                            {this.state.showMarkdownTutorial && (
                                <div>
                                    <Dialog
                                        open={this.state.showMarkdownTutorial}
                                        TransitionComponent={Transition}
                                        keepMounted
                                        onClose={this.handleClose}
                                        aria-labelledby="rich-text-tutorial-dialog"
                                        aria-describedby="rich-text-tutorial-dialog-description"
                                        fullScreen
                                    >
                                        <DialogTitle id="rich-text-tutorial-dialog-title">
                                            {"How to write Rich Text using Markdown?"}
                                        </DialogTitle>
                                        <DialogContent>
                                            {/* MARKDOWN TUTORIAL */}
                                            {/* <DialogContentText id="rich-text-tutorial-dialog-description">
                                                Output
                                            </DialogContentText> */}
                                            <Grid container spacing={16}>
                                                <Grid item sm={6} xs={12}>
                                                    <DialogContentText id="rich-text-tutorial-dialog-description">
                                                        Input
                                                    </DialogContentText>
                                                    <TextField
                                                        // label="Input"
                                                        multiline
                                                        placeholder="Test your Markdown here..."
                                                        className={classes.article}
                                                        value={this.state.tutorialState}
                                                        onChange={this.handleChange(
                                                            "tutorialState"
                                                        )}
                                                    />
                                                </Grid>
                                                <Grid item sm={6} xs={12}>
                                                    <DialogContentText id="rich-text-tutorial-dialog-description">
                                                        Output
                                                    </DialogContentText>
                                                    <div
                                                        className="markdown-parent"
                                                        dangerouslySetInnerHTML={this.getRawMarkup(
                                                            this.state.tutorialState
                                                        )}
                                                    />
                                                </Grid>
                                            </Grid>
                                        </DialogContent>
                                        <DialogActions>
                                            <Button
                                                onClick={this.handleClose}
                                                color="primary"
                                                variant="raised"
                                            >
                                                Close
                                            </Button>
                                        </DialogActions>
                                    </Dialog>
                                </div>
                            )}

                            {/* UPLOAD THUMBNAIL */}
                            {/* <Grid item xs={12} sm={12}>
                                <ThumbnailUpload
                                    cssProps={classes}
                                    thumbnailUrlAdded={this.thumbnailUrlAdded}
                                />
                            </Grid> */}

                            <Grid item xs={12} sm={6}>
                                <FormControl variant="outlined" fullWidth required>
                                    <InputLabel
                                        ref={ref => {
                                            this.InputLabelRef = ref
                                        }}
                                        htmlFor="outlined-age-simple"
                                    >
                                        Category
                                    </InputLabel>
                                    <Select
                                        value={this.state.category}
                                        onChange={this.handleChange("category")}
                                        input={
                                            <OutlinedInput
                                                labelWidth={this.state.labelWidth}
                                                name="category"
                                                id="outlined-age-simple"
                                            />
                                        }
                                    >
                                        <MenuItem value="article">Article</MenuItem>
                                        <MenuItem value="prediction">
                                            prediction
                                        </MenuItem>
                                    </Select>
                                </FormControl>
                            </Grid>

                            <Grid item xs={12} sm={6}>
                                <TextField
                                    label="Youtube video link(optional)"
                                    placeholder="paste here..."
                                    className={classes.selects}
                                    value={this.state.youtubeLink}
                                    onChange={this.handleChange("youtubeLink")}
                                    variant="outlined"
                                />
                            </Grid>
                        </Grid>
                    </Grid>

                    <Grid item xs={12} sm={6}>
                        {/* preview */}
                        <h1>Live Preview</h1>
                        <Card
                            className="live-preview-sticky"
                            style={{ overflow: "scroll", overflowX: "hidden" }}
                        >
                            <div className={classes.previewParent}>
                                <Typography
                                    variant="h4"
                                    color="textPrimary"
                                    className={classes.title}
                                >
                                    {this.state.title}
                                </Typography>
                                <Typography variant="subtitle2">
                                    by {this.state.author} on {this.state.date}
                                </Typography>
                                <div
                                    className="markdown-parent"
                                    dangerouslySetInnerHTML={this.getRawMarkup(
                                        this.state.article
                                    )}
                                />
                            </div>
                        </Card>
                    </Grid>
                </Grid>

                <Button
                    variant="contained"
                    color="primary"
                    className={classes.upload}
                    onClick={this.onUploadPost}
                    size="large"
                >
                    upload post
                </Button>

                {/* TODO: show result on snackbar */}
                {/* <Snackbar
                    anchorOrigin={{
                        vertical: "bottom",
                        horizontal: "center"
                    }}
                    open={this.state.openSnackbar}
                    style={{ marginBottom: 30 }}
                    autoHideDuration={3000}
                    // onClose={this.handleClose}
                    ContentProps={{
                        "aria-describedby": "message-id"
                    }}
                    message={
                        <span id="message-id">
                            {this.state.serverResponse.status === "success"
                                ? `Successfully submitted. Visit ${baseURL}${
                                      this.state.serverResponse.live_link_to_article
                                  }`
                                : "There were some problems, Please try again."}
                        </span>
                    }
                /> */}
            </div>
        )
    }
}

