function Transition(props) {
    return <Slide direction="up" {...props} />
}

class ImageUpload extends Component {
    constructor(props) {
        super(props)

        this.state = {
            imageUrl: [],
            // uploadClicked: false,
            actualFileData: [],
            imageUrlFromServer: [],
            loading: false,
            success: false,
            error: false
        }

        this.imgInputRef = React.createRef()
    }

    componentDidMount() {
        $("#fileupload").fileupload({
            url: uploadImagesPHP,
            dataType: "json",
            autoUpload: false,
            acceptFileTypes: /(\.|\/)(gif|jpe?g|png)$/i,
            maxFileSize: 999000,
            replaceFileInput: false,
            // add: function(e, data) {
            //     console.log(data.files[0].name)

            //     data.submit()
            // },
            done: function(e, data) {
                // console.log(data.result)

                this.setState({
                    imageUrl: [],
                    actualFileData: [],
                    loading: false
                    // imageUrlFromServer: [...this.state.imageUrlFromServer, ]
                })

                // pass data.result up to MarkDown component
                this.props.imageUrlAdded(data.result)

                // console.log($("#fileupload"))
            }.bind(this)
        })
    }

    onImageChange = e => {
        for (let i = 0; i < this.imgInputRef.files.length; i++) {
            const file = this.imgInputRef.files[i]

            const reader = new FileReader()
            reader.onloadend = () => {
                if (this.state.imageUrl.includes(reader.result) === true) {
                    alert("This image has been already selected")
                } else {
                    this.setState(
                        {
                            imageUrl: [...this.state.imageUrl, reader.result],
                            // uploadClicked: true
                            actualFileData: [
                                ...this.state.actualFileData,
                                this.imgInputRef.files[i]
                            ]
                        },
                        () => {
                            // console.log(this.state)
                        }
                    )
                }
            }
            if (file) {
                reader.readAsDataURL(file)
            }
        }
    }

    cancelImage = index => e => {
        const currentImgArray = this.state.imageUrl
        currentImgArray.splice(index, 1)

        const currentActualFileDataArray = this.state.actualFileData
        currentActualFileDataArray.splice(index, 1)

        this.setState(
            {
                imageUrl: [...currentImgArray],
                // uploadClicked: false
                actualFileData: [...currentActualFileDataArray]
            },
            () => {
                // console.log(this.state)
            }
        )
    }

    // startUpload = e => {
    //     if (this.state.actualFileData.length <= 0) {
    //         alert("You have not selected any images")
    //     } else {
    //         // console.log(this.imgInputRef.files)

    //         $("#fileupload").fileupload("send", { files: this.state.actualFileData })

    //         if (!this.state.loading) {
    //             this.setState({
    //                 loading: true
    //             })
    //         }
    //     }
    // }

    startUpload = e => {
        if (this.state.actualFileData.length <= 0) {
            alert("You have not selected any images")
        // } 
        // else if (this.state.actualFileData[0].size >= 5242880) {
        //     // file is larger than 5 MB
        //     alert(
        //         "Selected thumbnail is larger than 5 MB. Compress the image or use a different one."
        //     )
        } else {
            // check if any selected file is larger than 5 MB
            for (let i = 0; i < this.state.actualFileData.length; i++) {
                console.log("for running")
                if (this.state.actualFileData[i].size >= 5242880) {
                    console.log("if running")
                    alert(
                        "Selected image is larger than 5 MB. Compress the image or use a different one."
                    )

                    return;
                }
            }

            // 
            $("#fileupload").fileupload("send", { files: this.state.actualFileData })

            if (!this.state.loading) {
                this.setState({
                    loading: true
                })
            }
        }
    }

    render() {
        return (
            <React.Fragment>
                <div>
                    {this.state.imageUrl.map((image, index) => {
                        return (
                            <div className="upload-preview-image-parent" key={image}>
                                <img src={image} className="upload-preview-image" />
                                <span
                                    onClick={this.cancelImage(index)}
                                    className="upload-preview-cancel"
                                >
                                    <Close color="inherit" />
                                </span>
                            </div>
                        )
                    })}
                </div>

                <div style={{ width: "100%" }}>
                    <input
                        ref={c => {
                            this.imgInputRef = c
                        }}
                        accept="image/*"
                        className={this.props.cssProps.input}
                        id="fileupload"
                        multiple
                        type="file"
                        onChange={this.onImageChange}
                        // value=""

                        // onInput={this.onImageChange}
                    />
                    <label htmlFor="fileupload">
                        <Button
                            variant="outlined"
                            color="secondary"
                            component="span"
                            className={this.props.cssProps.button}
                            // onChange={this.onImageChange}
                        >
                            Select images
                        </Button>
                    </label>

                    <Button
                        variant="outlined"
                        color="primary"
                        component="span"
                        className={`${this.props.cssProps.button}`}
                        // disabled={this.state.loading}
                        onClick={this.startUpload}
                    >
                        Start Upload
                    </Button>
                    {this.state.loading && <LinearProgress />}
                </div>
            </React.Fragment>
        )
    }
}

