import { Paper, Tab, Tabs, withStyles } from "@material-ui/core"
import React, { Component } from "react"
import { Redirect } from "react-router-dom"
import "../../stylesheets/css/edit.css"
import MarkDown from "./MarkDown"
import ImageUpload from "./ImageUpload"

class Edit extends Component {
    constructor(props) {
        super(props)
        this.state = {
            redirect: false,
            value: 0
        }
    }

    componentWillMount() {
        if (JSON.parse(sessionStorage.getItem("userData"))) {
        } else {
            this.setState(function() {
                return {
                    redirect: true
                }
            })
        }
    }

    handleTabChange = (event, value) => {
        this.setState({ value })
    }

    render() {
        const { value } = this.state
        const { classes } = this.props
        if (this.state.redirect) {
            return <Redirect to={"/admin"} />
        }

        return (
            <div className="edit-hero">
                {/* <h1>welcome to draft js editor</h1> */}
                {/* <DraftJsEditor /> */}

                {/* <LoadableMarkDown /> */}
                <div>
                    <Paper className={classes.root}>
                        <Tabs
                            value={value}
                            onChange={this.handleTabChange}
                            indicatorColor="primary"
                            textColor="primary"
                            centered
                        >
                            {/* <Tab label="Upload Images" /> */}
                            <Tab label="Upload Post/Images" />
                            {/* <Tab label="Upload PDF" /> */}
                        </Tabs>
                    </Paper>
                </div>

                {/* {value === 0 && <ImageUpload />} */}

                {value === 0 && <MarkDown />}
            </div>
        )
    }
}

const styles = theme => ({
    root: {
        // flexGrow: 1,
        margin: "0 auto",
        backgroundColor: theme.palette.background.paper,
        width: "80%"
    },
    container: {
        display: "flex",
        flexWrap: "wrap"
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: "100%"
    },
    dense: {
        marginTop: 19
    },
    menu: {
        width: 200
    }
})

// const LoadableMarkDown = Loadable({
//     loader: () => import("./MarkDown"),
//     loading: () => <LoadableLoading />
// })

export default withStyles(styles)(Edit)
