import { CircularProgress, Grid } from "@material-ui/core"
import React from "react"
import { Route, Switch } from "react-router-dom"
import "../stylesheets/header.css"
import Edit from "./admin/Edit"
import Login from "./admin/Login"
import About from "./blog/About"
import BlogPosts from "./blog/BlogPosts"
import Contact from "./blog/Contact"
import MainNews from "./blog/MainNews"
import VerticalNav from "./blog/VerticalNav"

function ButtonAppBar(props) {
    return (
        <div>
            <Grid container>
                <Grid item md={3} sm={12}>
                    <VerticalNav />
                </Grid>
                <Grid item md={9} sm={12}>
                    <div className="header-master">
                        <Switch>
                            <Route exact path="/" component={About} />
                            <Route exact path="/about" component={About} />
                            <Route exact path="/blog" component={BlogPosts} />
                            <Route exact path="/contact" component={Contact} />
                            <Route exact path="/admin" component={Login} />
                            <Route exact path="/edit" component={Edit} />
                            <Route exact path="/:pkey" component={MainNews} />
                        </Switch>
                    </div>
                </Grid>
            </Grid>

            {/* <div>
                <Footer />
            </div> */}
        </div>
    )
}

// const LoadableHome = Loadable({
//     loader: () => import("./Home"),
//     loading: () => <Loading />
// })

const Loading = props => {
    if (props.error) {
        return (
            <div>
                Error! <buton onClick={props.retry}>Retry</buton>
            </div>
        )
    } else if (props.pastDelay) {
        return (
            <div>
                <h1>loading...</h1>
            </div>
        )
    } else if (props.timedOut) {
        return (
            <div>
                Taking a long time... <button onClick={props.retry}>Retry</button>
            </div>
        )
    } else {
        return (
            <div style={{ width: "100%", margin: "0px auto", padding: "30px" }}>
                <CircularProgress />
            </div>
        )
    }
}

export default ButtonAppBar
