import { Button, Card, Typography, withStyles } from "@material-ui/core"
import hljs from "highlight.js"
import "highlight.js/styles/github-gist.css"
import $ from "jquery"
import React, { Component } from "react"
import { Redirect } from "react-router-dom"
import Remarkable from "remarkable"
import { deletePostPHP, particularNewsPHP } from "../../AppClass"
// import "../stylesheets/homePage.css"
import "../../stylesheets/markdown.css"
// import "../stylesheets/particular-news.css"
// import "../stylesheets/you-might-also-like.css"
import VideoPlayer from "../video/VideoPlayer"

class MainNews extends Component {
    constructor(props) {
        super(props)

        this.state = {
            data: [],
            isAdmin: false,
            redirect: false
        }
    }

    componentWillMount() {
        if (JSON.parse(sessionStorage.getItem("userData"))) {
            this.setState(function() {
                return {
                    isAdmin: true
                }
            })
        }
    }

    componentDidMount() {
        $.ajax({
            type: "POST",
            url: particularNewsPHP,
            // contentType: "application/json; charset=utf-8",
            // dataType: "json",
            data: {
                pkey: this.props.match.params.pkey
            },
            success: function(res) {
                // console.log(JSON.parse(res))

                let parsedRes = JSON.parse(res)

                console.log(parsedRes)

                if (parsedRes.status !== "success") {
                    this.setState(function() {
                        return { redirect: true }
                    })
                    return
                } else {
                    this.setState(function(state, props) {
                        // console.log([parsedRes.main_post[0], ...parsedRes.recent_stories])
                        return {
                            data: parsedRes.main_post[0],
                            // firstPost: parsedRes.main_post[0],
                            // recentStories: parsedRes.recent_stories[0]
                        }
                    }, () => console.log(this.state.data))
                }

            }.bind(this),
            error: function(error) {
                console.log(error)
            }.bind(this)
        })

        // scroll to top
        window.scroll({ top: 0, left: 0, behavior: "instant" })

        // $(window).on("scroll", this.scrollToLoad)
    }

    getRawMarkup(passedState) {
        var md = new Remarkable("full", {
            html: true, // Enable HTML tags in source
            xhtmlOut: false, // Use '/' to close single tags (<br />)
            breaks: false, // Convert '\n' in paragraphs into <br>
            langPrefix: "language-", // CSS language prefix for fenced blocks
            linkify: true, // autoconvert URL-like texts to links
            linkTarget: "", // set target to open link in

            // Enable some language-neutral replacements + quotes beautification
            typographer: true,

            // Double + single quotes replacement pairs, when typographer enabled,
            // and smartquotes on. Set doubles to '«»' for Russian, '„“' for German.
            quotes: "“”‘’",

            // Highlighter function. Should return escaped HTML,
            // or '' if input not changed
            highlight: function(str, lang) {
                if (lang && hljs.getLanguage(lang)) {
                    try {
                        return hljs.highlight(lang, str).value
                    } catch (__) {}
                }

                try {
                    return hljs.highlightAuto(str).value
                } catch (__) {}

                return "" // use external default escaping
            }
        })

        return { __html: md.render(passedState) }
    }

    deletePost = e => {
        if (window.confirm("Do you really want to delete the post?")) {
            console.log("post delete confirmed...", this.state.data.pkey)

            // delete post only when OK is clicked
            $.ajax({
                url: deletePostPHP,
                method: "POST",
                data: {
                    pkey: this.state.data.pkey
                },
                success: function(res) {
                    console.log(res)
                    // res will return 1 if delete op is successful
                    if (res == 1) {
                        this.setState({
                            redirect: true
                        })

                        alert("Post successfully deleted.")
                    }
                }.bind(this),
                error: function(err) {}
            })
        }
    }

    render() {
        console.log("render getting called")

        const { classes } = this.props
        const { isAdmin, redirect } = this.state

        const timestamp = this.state.data.time
        const date = new Date(timestamp * 1000)

        if (redirect) {
            return <Redirect to={"/"} />
        }

        return (
            <div>
                <Card>
                    {/* {this.state.data.video !== "" && (
                        <VideoPlayer video={this.state.data.video} />
                    )} */}
                    <div style={{margin: "30px"}}>
                        {/* include video if link is present */}

                        {/* <Typography
                            variant="subheading"
                            color="textSecondary"
                            gutterBottom
                            style={{ marginTop: 30 }}
                        >
                            {
                                GANASAKTI_CATEGORIES.find(
                                    category => this.state.data.category === category.name
                                ).value
                            }{" "}
                            /
                        </Typography> */}

                        <Typography
                            variant="h2"
                            // classes={{
                            //     root: classes.root // class name, e.g. `classes-nesting-root-x`
                            // }}
                            className={classes.responsiveTitle}
                            color="textPrimary"
                            gutterBottom
                        >
                            {this.state.data.title}
                        </Typography>

                        <Typography
                            variant="subheading"
                            color="textSecondary"
                            style={{ marginBottom: 60 }}
                        >
                            {this.state.data.author} / {date.toLocaleDateString()}
                        </Typography>

                        {this.state.data.video !== "" && (
                            <VideoPlayer video={this.state.data.video} />
                        )}

                        <div
                            className="markdown-parent"
                            dangerouslySetInnerHTML={this.getRawMarkup(
                                this.state.data.post
                            )}
                            style={{ marginTop: 60 }}
                        />

                        {/* facebook share */}
                        {isAdmin && (
                            <div style={{ margin: "30px 0" }}>
                                <Button
                                    color="primary"
                                    variant="outlined"
                                    onClick={this.deletePost}
                                >
                                    Delete Post
                                </Button>
                            </div>
                        )}
                    </div>
                    {/* <Facebook data={this.state.data} /> */}
                </Card>
            </div>
        )
    }
}

const styles = theme => ({
    responsiveTitle: {
        [theme.breakpoints.down("sm")]: {
            fontSize: 30
        },
        marginTop: 40
    }
})

