import { Card, LinearProgress, Typography, Snackbar } from "@material-ui/core"
import Button from "@material-ui/core/Button"
import IconButton from "@material-ui/core/IconButton"
import { withStyles } from "@material-ui/core/styles"
import TextField from "@material-ui/core/TextField"
import CloseIcon from "@material-ui/icons/Close"
import $ from "jquery"
import React, { Component } from "react"
import { Redirect } from "react-router-dom"
import { loginPHP } from "../../AppClass"
import "../../stylesheets/css/admin.css"

class Login extends Component {
    constructor(props) {
        super(props)

        this.state = {
            username: "",
            password: "",
            showPassword: false,
            userData: [],
            redirect: false,
            loading: false,
            showSnackbar: false
        }
    }

    handleChange = name => event => {
        this.setState(
            {
                [name]: event.target.value
            },
            function() {
                // console.log(this.state)
            }
        )
    }

    handleClickShowPassword = () => {
        this.setState(state => ({ showPassword: !state.showPassword }))
    }

    handleSnackbarClose = event => {
        this.setState({
            showSnackbar: false
        })
    }

    submitLoginData = () => {
        if (this.state.username && this.state.password) {
            this.setState({
                loading: true
            })

            $.ajax({
                type: "POST",
                url: loginPHP,
                // contentType: "application/json; charset=utf-8",
                // dataType: "json",
                data: {
                    username: this.state.username,
                    password: this.state.password
                },
                success: function(res) {
                    let result = JSON.parse(res)

                    this.setState(function() {
                        return {
                            userData: result,
                            loading: false
                        }
                    })

                    // console.log(this.state)

                    if (this.state.userData.log === "success") {
                        // console.log(this.state.userData)
                        // console.log("success")

                        sessionStorage.setItem(
                            "userData",
                            JSON.stringify(this.state.userData)
                        )
                        this.setState(function() {
                            return {
                                redirect: true
                            }
                        })
                    } else {
                        this.setState({
                            showSnackbar: true
                        })
                    }
                }.bind(this),
                error: function(error) {
                    console.log(error)
                    alert("Failed to load resource: net::ERR_CONNECTION_REFUSED")
                    this.setState({
                        loading: false
                    })
                }.bind(this)
            })
        }
    }

    render() {
        const { classes } = this.props
        const { loading, username, password } = this.state

        if (this.state.redirect) {
            return <Redirect to={"/edit"} />
        }
        return (
            <div>
                <div className="login-hero">
                    <Card className="login-card">
                        <Typography variant="h3" color="primary">
                            Admin Login
                        </Typography>

                        <section>
                            <TextField
                                id="with-placeholder"
                                label="Username"
                                placeholder="Enter username"
                                variant="outlined"
                                className={classes.textField}
                                value={username}
                                margin="normal"
                                onChange={this.handleChange("username")}
                                fullWidth
                            />
                        </section>

                        <section>
                            <TextField
                                id="outlined-password-input"
                                label="Password"
                                className={classes.textField}
                                type="password"
                                autoComplete="current-password"
                                margin="normal"
                                variant="outlined"
                                fullWidth
                                onChange={this.handleChange("password")}
                            />
                        </section>

                        <section>
                            <Button
                                variant="outlined"
                                color="primary"
                                className={classes.button}
                                onClick={this.submitLoginData}
                            >
                                Log in
                            </Button>
                        </section>
                    </Card>
                </div>

                {loading && (
                    <div className={classes.linearProgress}>
                        <LinearProgress />
                    </div>
                )}

                <Snackbar
                    anchorOrigin={{
                        vertical: "bottom",
                        horizontal: "left"
                    }}
                    open={this.state.showSnackbar}
                    autoHideDuration={4000}
                    onClose={this.handleSnackbarClose}
                    ContentProps={{
                        "aria-describedby": "message-id"
                    }}
                    message={<span id="message-id">Wrong username or password</span>}
                    action={
                        <IconButton
                            key="close"
                            aria-label="Close"
                            color="inherit"
                            className={classes.close}
                            onClick={this.handleSnackbarClose}
                        >
                            <CloseIcon />
                        </IconButton>
                    }
                />
            </div>
        )
    }
}

const styles = theme => ({
    container: {
        display: "flex",
        flexWrap: "wrap"
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: 200
    },
    root: {
        display: "flex",
        flexWrap: "wrap"
    },
    margin: {
        margin: theme.spacing.unit
    },

    textField: {
        flexBasis: 200
    },
    button: {
        margin: theme.spacing.unit
    }
})

export default withStyles(styles)(Login)
