import React, { Component } from "react"
import "../../stylesheets/css/vertical-nav.css"
import { Typography, Card, Divider } from "@material-ui/core"
import ZubleeAvatar from "../../images/team/Amitabh Gohain.jpg"
import { withRouter } from "react-router-dom"

class VerticalNav extends Component {
    handleNavClick = route => event => {
        this.props.history.push("/" + route)
    }

    render() {
        return (
            // <div className="vertical-nav-hero">
            <Card className="vertical-nav-hero">
                <section className="avatar-parent">
                    <img src={ZubleeAvatar} />
                </section>

                <section className="title">
                    <Typography variant="h4">Sukleswar Deka</Typography>
                </section>

                <section className="divider">
                    <Divider />
                </section>

                <section className="nav">
                    <Typography
                        variant="h4"
                        color="textSecondary"
                        gutterBottom
                        onClick={this.handleNavClick("about")}
                    >
                        About
                    </Typography>
                    <Typography
                        variant="h4"
                        color="textSecondary"
                        gutterBottom
                        onClick={this.handleNavClick("blog")}
                    >
                        Blog
                    </Typography>
                    <Typography
                        variant="h4"
                        color="textSecondary"
                        gutterBottom
                        onClick={this.handleNavClick("contact")}
                    >
                        Contact
                    </Typography>
                </section>
            </Card>

            // </div>
        )
    }
}

export default withRouter(VerticalNav)
