import React, { Component } from "react"
import $ from "jquery"
import "../../stylesheets/all-news-one-category.css"
import { blogPostsPHP, url } from "../../AppClass"
import { withRouter } from "react-router"
// import "../stylesheets/you-might-also-like.css"
import { Card, Button } from "@material-ui/core"

class AllNewsOneCategory extends Component {
    constructor(props) {
        super(props)

        this.state = {
            skip: 0,
            posts: [],
            shouldLoadMore: true
        }
    }

    componentDidMount() {
        // console.log(this.props.category)

        $.ajax({
            type: "POST",
            url: blogPostsPHP,
            // contentType: "application/json; charset=utf-8",
            // dataType: "json",
            data: {
                // category: this.props.category,
                skip: this.state.skip
            },
            success: function(res) {
                this.setState(function(state, props) {
                    return {
                        posts: JSON.parse(res),
                        skip: state.skip + 5
                    }
                })
            }.bind(this),
            error: function(error) {
                console.log(error)
            }
        })
    }

    componentWillReceiveProps(nextProps, nextContext) {
        // console.log(nextProps)

        if (this.props.category !== nextProps.category) {
            this.setState(function(state) {
                return {
                    skip: 0,
                    shouldLoadMore: true
                }
            })

            this.setState(
                {
                    skip: 0,
                    shouldLoadMore: true
                },
                function() {
                    $.ajax({
                        type: "POST",
                        url: blogPostsPHP,
                        // contentType: "application/json; charset=utf-8",
                        // dataType: "json",
                        data: {
                            category: nextProps.category,
                            skip: this.state.skip
                        },
                        success: function(res) {
                            this.setState(function(state, props) {
                                return {
                                    posts: JSON.parse(res),
                                    skip: state.skip + 5
                                }
                            })
                        }.bind(this),
                        error: function(error) {
                            console.log(error)
                        }
                    })
                }
            )

        }
    }

    onNewsClick = post => {
        this.props.history.push("/" + post.pkey)
    }

    loadMoreNews = () => {
        $.ajax({
            type: "POST",
            url: blogPostsPHP,
            // contentType: "application/json; charset=utf-8",
            // dataType: "json",
            data: {
                category: this.props.category,
                skip: this.state.skip
            },
            success: function(res) {
                // TODO:
                let result = JSON.parse(res)
                // console.log(result.length)

                result.map(res => {
                    this.setState(function(state) {
                        return {
                            posts: [...state.posts, res]
                            // skip: state.skip + 5
                        }
                    })
                })

                // console.log(result.length)

                if (result.length < 5) {
                    this.setState(function() {
                        return {
                            shouldLoadMore: false
                        }
                    })
                }

                this.setState(function(state) {
                    return {
                        // posts: [...state.posts, result],
                        skip: state.skip + 5
                    }
                })

                // console.log(this.state.posts)
            }.bind(this),
            error: function(error) {
                console.log(error)
            }
        })
    }

    render() {
        return (
            <div>
                {/* TODO: make the news layout */}
                {this.state.posts.map(post => {
                    let date = new Date(post.time * 1000)
                    return (
                        <Card
                            className="onoc-parent"
                            key={Math.random() * Math.random()}
                            onClick={() => this.onNewsClick(post)}
                        >
                            <div className="onoc-text-container">
                                <h2>{post.title}</h2>
                                <i>
                                    <p>
                                        by <b>{post.author}</b> on{" "}
                                        <b>
                                            {date.getDate()}/{date.getMonth() + 1}/
                                            {date.getFullYear()}
                                        </b>
                                    </p>
                                </i>
                            </div>
                            {/* <div className="onoc-image-container">
                                <object
                                    data={`${url}/images/thumbnail/${post.thumbnail}`}
                                    className="onoc-img"
                                    type="image/jpg"
                                    // alt="image"
                                    // onLoad={this.onImageLoad}
                                />
                            </div> */}
                        </Card>
                    )
                })}

                {/* this.state.posts.length > 0 && */ this.state.shouldLoadMore && (
                    // <button className="load-more-news-button" onClick={this.loadMoreNews}>
                    //     LOAD MORE
                    // </button>
                    <Button
                        onClick={this.loadMoreNews}
                        variant="outlined"
                        color="primary"
                    >
                        Load More
                    </Button>
                )}
            </div>
        )
    }
}

export default withRouter(AllNewsOneCategory)
